.main {
    padding: 20px;
    height: 100vh;
    background-color: #f4f4f4;
}

.layout {
    display: flex;
}

.sidebar {
    min-height: 100%;
    width: 300px;
    border-radius: 8px;
    background-color: #fff;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    margin-right: 20px;
}

.content {
    flex-grow: 1;
    height: 100%;
}

.header {
    height: 60px;
    margin-bottom: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    padding: 0 20px; /* Adjusted padding for better spacing */
}

.body {
    height: calc(100% - 80px);
    margin-bottom: 20px;
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.1);
    background-color: #f4f4f4;
    border-radius: 8px;
}

.menu {
    margin-top: 20px;
}

.logo h6 {
    font-size: 1.5rem;
    text-align: center;
    margin: 20px 0;
    color: #333;
}

.menu-item {
    margin-top: 20px;
}

.menu-item a {
    color: #333;
    text-decoration: none;
    font-size: 1.2rem;
    transition: color 0.3s ease;
}

.menu-item i {
    font-size: 1.2rem;
    margin: 0 15px;
}

.menu-item a:hover {
    color: #3498db;
}

.active {
    background-color: #3498db;
    padding: 10px;
    border-radius: 5px;
}

.active a,
.active i {
    color: #fff;
}

.header-content {
    display: flex;
    align-items: center;
    height: 100%;
}

.header-content i {
    margin-right: 10px;
    font-size: 1.2rem;
}

.header-content a {
    text-decoration: none;
    color: #3498db;
    margin: 0 10px;
    text-transform: uppercase;
    font-size: 0.9rem;
}

/* New style for the bell icon */
.header-content .bell-icon {
    margin-right: 10px;
}
