.form-container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    background-color: #f8f9fa; /* Light gray background */
}

.register-form {
    border: 2px solid #3498db; /* Blue border color */
    border-radius: 10px; /* Rounded corners */
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Subtle box shadow */
    padding: 20px; /* Reduced padding for smaller screens */
    width: 80%; /* Adjusted width for smaller screens */
    max-width: 400px; /* Set a maximum width to avoid excessive stretching */
    background-color: #ffffff; /* White background */
}

.register-form h2 {
    text-align: center;
    color: #333; /* Dark text color */
}

/* Style the form elements within .register-form */
.register-form label {
    display: block;
    margin-bottom: 10px;
    color: #555; /* Medium gray text color */
}

.register-form input {
    width: 100%;
    padding: 10px;
    margin-bottom: 15px;
    box-sizing: border-box;
    border: 1px solid #ccc; /* Light gray border */
    border-radius: 5px;
}

.register-form button {
    width: 100%;
    padding: 12px;
    background-color: #3498db; /* Blue button color */
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.register-form button:hover {
    background-color: #2980b9; /* Darker shade on hover */
}
